import {DefineComponent, defineComponent, ExtractPropTypes, getCurrentInstance, h, InjectionKey, PropType, provide, PublicProps, SlotsType} from 'vue';
import {Combobox} from 'vrix.js';
import ComponentWrapper from '@/components/util/ComponentWrapper';
import {bindNative, mountNative, NativeInstance} from '../mixins/NativeComponent';
import {defaultInputProps, defaultSuggestProps, makeDefaultMethods, NativeAutocompleteEvents} from '../VxAutocomplete/VxAutocomplete';
import ItemTemplateWrapper from '@/components/util/ItemTemplateWrapper';
import VxChip from '@/components/VxTokenizer/VxChip';
import VxSuggestItem from '@/components/VxAutocomplete/VxSuggestItem';
import VxTextChip from '@/components/VxCombobox/VxTextChip';
import {useModel} from '@/components/util/useModel';
import useItemTemplate from '@/components/util/useItemTemplate';
import {defaultSelectableProps, ItemResult, useSelectableModels} from '../mixins/Selectable';
import {NativeTokenizerEvents, TokenizerEvents, TokenizerMethods, TokenizerSlots} from '../VxTokenizer/VxTokenizer';

export declare type ComboboxGetters = {
    /**
     * `VxCombobox` 감싸고 있는 Combobox 인스턴스입니다.
     */
    nativeInstance: Combobox;
};

export declare interface ComboboxMethods extends TokenizerMethods {}

export declare type ComboboxEvents<T = any> = TokenizerEvents<T> & {};

export declare type ComboboxSlots = TokenizerSlots & {};

interface ComboboxProvider {}

export const VxComboboxSymbol: InjectionKey<ComboboxProvider> = Symbol.for('ComboboxInstance');

// eslint-disable-next-line @typescript-eslint/no-unused-vars
const {suggestSource, ...otherSuggestProps} = defaultSuggestProps;
export const defaultComboboxProps = {
    /**
     * 표시되는 아이템의 배열입니다.
     * 'labelField' 속성을 이용하여 표시되는 텍스트를 결정합니다.
     */
    items: {
        type: Array as PropType<Array<ItemResult>>
    },
    /**
     * 사용자가 입력한 문자열에 따라 콤보박스에서 표시되는 항목을 필터링할 수 있는지 여부입니다.
     */
    filterable: {
        type: Boolean as PropType<Boolean>,
        default: false
    },
    /**
     * 표시된 아이템의 삭제버튼 활성화 여부입니다.
     */
    chipRemovable: {
        type: Boolean as PropType<Boolean>,
        default: false
    }
};

export declare type ComboboxPropsType = typeof defaultInputProps & typeof otherSuggestProps & typeof defaultSelectableProps & typeof defaultComboboxProps;

const VxComboboxImpl = defineComponent({
    name: 'VxCombobox',
    emits: [...NativeTokenizerEvents, 'update:modelValue', 'update:selectedItem', 'update:selectedItems', 'update:selectedValue', 'update:inputValue'],
    props: {
        ...defaultInputProps,
        ...otherSuggestProps,
        ...defaultSelectableProps,
        ...defaultComboboxProps
    },
    setup(props) {
        const instance = getCurrentInstance();
        const nativeInstance = new Combobox() as NativeInstance<Combobox>;
        const suggestItemFactory = ComponentWrapper.factory(instance, VxSuggestItem as any, ItemTemplateWrapper);
        const chipFactory = ComponentWrapper.factory(instance, VxChip as any, ItemTemplateWrapper);
        const singleChipFactory = ComponentWrapper.factory(instance, VxTextChip as any, ItemTemplateWrapper);
        const {
            itemTemplate: suggestItemTemplate,
            hookRenderElement: hookRenderSuggestElement,
            hookFreeElement: hookFreeSuggestElement
        } = useItemTemplate(nativeInstance.suggestList, suggestItemFactory);

        bindNative(nativeInstance, props);
        mountNative(instance.proxy, nativeInstance, {
            events: NativeAutocompleteEvents,
            options: {
                ...props,
                chipTemplate: (item: any, index: number): HTMLElement => {
                    const wrapper = chipFactory({item, nativeInstance});
                    return wrapper.element;
                },
                singleChipTemplate: (item: any, index: number): HTMLElement => {
                    const wrapper = singleChipFactory({item, nativeInstance});
                    return wrapper.element;
                },
                suggestItemTemplate,
                hookRenderSuggestElement,
                hookFreeSuggestElement
            }
        });
        useSelectableModels(props, nativeInstance);
        useModel(props, 'inputValue', nativeInstance, 'inputValue', 'value-change');
        provide(VxComboboxSymbol, {});

        return {
            nativeInstance,
            ...makeDefaultMethods(nativeInstance)
        };
    },
    render() {
        return h('div', {class: 'x-combobox-wrap'});
    }
});

const VxCombobox = VxComboboxImpl as unknown as DefineComponent<
    ComboboxPropsType,
    {},
    ComboboxGetters,
    {},
    ComboboxMethods,
    {},
    {},
    ComboboxEvents,
    string,
    PublicProps,
    ExtractPropTypes<ComboboxPropsType>,
    ExtractPropTypes<ComboboxPropsType>,
    SlotsType<ComboboxSlots>
>;

export default VxCombobox;
