import {DefineComponent, defineComponent, ExtractPropTypes, getCurrentInstance, h, InjectionKey, PropType, provide, PublicProps, SlotsType} from 'vue';
import {CoordinateMode, DatePicker, DateRange, DateType, FormatType, Popup} from 'vrix.js';
import ComponentWrapper from '@/components/util/ComponentWrapper';
import {bindNative, mountNative} from '../mixins/NativeComponent';
import VxDateChooserItem from '@/components/VxDateChooser/VxDateChooserItem';
import {DateChooserEvents, DateChooserMethods, DateChooserPropsType, DateChooserSlot, defaultDateChooserProps} from '../VxDateChooser/VxDateChooser';
import ItemTemplateWrapper from '@/components/util/ItemTemplateWrapper';
import useItemTemplate from '@/components/util/useItemTemplate';
import {useModel} from '@/components/util/useModel';

export declare type DatePickerGetters = {
    /**
     * `VxDatePicker` 감싸고 있는 DatePicker 인스턴스입니다.
     */
    nativeInstance: DatePicker;
};

export declare interface DatePickerMethods extends DateChooserMethods {
    /**
     * 날짜선택 팝업을 엽니다.
     */
    open(): Popup;
    /**
     * 날짜선택 팝업을 닫습니다.
     */
    close(reason?: any): Popup;
}

export declare type DatePickerEvents = DateChooserEvents & {
    /**
     * 날짜선택 팝업이 열리기 전에 발생합니다.
     */
    'date-picker-opening': (event: CustomEvent<DateRange>) => void;
    /**
     * 날짜선택 팝업이 닫히기 전에 발생합니다.
     */
    'date-picker-closing': (event: CustomEvent<DateRange>) => void;
    /**
     * 날짜선택 팝업이 열릴때 발생합니다.
     */
    'date-picker-open': (event: CustomEvent<DateRange>) => void;
    /**
     * 날짜선택 팝업이 닫힐때 발생합니다.
     */
    'date-picker-close': (event: CustomEvent<DateRange>) => void;
    /**
     * 날짜를 선택했을 때 발생합니다.
     */
    'date-picker-select': (event: CustomEvent<DateRange>) => void;
};

export declare type DatePickerSlot = DateChooserSlot & {};

interface DatePickerProvider {}
export const VxDatePickerSymbol: InjectionKey<DatePickerProvider> = Symbol.for('DatePickerInstance');
export const NativeDatePickerEvents: Array<string> = [
    'state-change',
    'selection-changing',
    'selection-change',
    'date-picker-opening',
    'date-picker-closing',
    'date-picker-open',
    'date-picker-close',
    'date-picker-select'
];

export const defaultDatePickerProps = {
    /**
     * 입력 필드의 레이블 텍스트입니다.
     */
    label: {
        type: String,
        default: ''
    },
    /**
     * 입력필드에 값이 있을때 초기화 버튼 활성화 여부입니다.
     */
    clearable: {
        type: Boolean as PropType<boolean>,
        default: false
    },
    /**
     * 입력 필드에 값이 입력되지 않는 경우 표시되는 텍스트입니다.
     */
    placeholder: {
        type: String as PropType<string>
    },
    /**
     * 타이틀에 표시되는 날짜의 형식입니다.
     * `DateChooser.setFormatter` 설정된 포멧터에 의해 결정됩니다.
     * ```typescript
     * datePicker.titlePatterns = {day : 'YYYY.MM.DD', month: 'YYYY.MM',  year:'YYYY'}
     * ```
     */
    titlePatterns: {
        type: Object as PropType<FormatType>
    },
    /**
     * 팝업의 좌표 시스템을 설정합니다.
     * - 'global' 팝업을 전역 좌표계에 배치합니다.
     * - 'relative' 팝업을 상대적인 위치에 배치합니다('anchor' 속성이 지정되어 있을때만 가능합니다.).
     * - 'none' 팝업에 특정 좌표 시스템을 적용하지 않습니다.
     * 기본 값은 'relative' 입니다.
     */
    coordinateMode: {
        type: String as PropType<CoordinateMode>,
        default: 'relative'
    }
};

export declare type DatePickerPropsType = typeof defaultDatePickerProps & DateChooserPropsType;

const VxDatePickerImpl = defineComponent({
    name: 'VxDatePicker',
    props: {
        ...defaultDateChooserProps,
        ...defaultDatePickerProps
    },
    emits: [...NativeDatePickerEvents, 'update:modelValue', 'update:selectedDate', 'update:selectedDateString', 'update:selectedRange'],
    setup(props): DatePickerGetters & DatePickerMethods {
        const instance = getCurrentInstance();
        const nativeInstance = new DatePicker();

        let options = null;
        if (instance.slots.default) {
            const itemFactory = ComponentWrapper.factory(instance, VxDateChooserItem, ItemTemplateWrapper);
            const {itemTemplate: dateChooserItemTemplate, hookRenderElement: dateChooserHookRenderElement, hookFreeElement: dateChooserHookFreeElement} = useItemTemplate(nativeInstance, itemFactory);
            options = {...props, dateChooserItemTemplate, dateChooserHookRenderElement, dateChooserHookFreeElement};
        } else {
            options = props;
        }

        bindNative(nativeInstance, props);
        mountNative(instance.proxy, nativeInstance, {events: NativeDatePickerEvents, options});
        useModel(props, 'modelValue', nativeInstance, props.allowRange ? 'selectedRange' : 'selectedDate', 'selection-change');
        useModel(props, 'selectedDate', nativeInstance, 'selectedDate', 'selection-change');
        useModel(props, 'selectedDateString', nativeInstance, 'selectedDateString', 'selection-change');
        if (props.allowRange) {
            useModel(props, 'selectedRange', nativeInstance, 'selectedRange', 'selection-change');
        }
        provide(VxDatePickerSymbol, {});

        return {
            nativeInstance,
            clear(): void {
                this.nativeInstance.clear();
            },
            reset(): void {
                this.nativeInstance.reset();
            },
            setCurrentDate(date: Date): void {
                this.nativeInstance.setCurrentDate(date);
            },
            getState(): DateType {
                return this.nativeInstance.getState();
            },
            open(): Popup {
                return this.nativeInstance.open();
            },
            close(reason?: any): Popup {
                return this.nativeInstance.close();
            }
        };
    },
    render() {
        return h('div', {class: 'x-date-picker-wrap'});
    }
});

const VxDatePicker = VxDatePickerImpl as unknown as DefineComponent<
    DatePickerPropsType,
    {},
    DatePickerGetters,
    {},
    DatePickerMethods,
    {},
    {},
    DatePickerEvents,
    string,
    PublicProps,
    ExtractPropTypes<DatePickerPropsType>,
    ExtractPropTypes<DatePickerPropsType>,
    SlotsType<DatePickerSlot>
>;

export default VxDatePicker;
