export declare type ListEvents<T = any> = SelectionEvents<T> & {
    /**
     * 컬렉션 내부의 아이템이 변경되었을 때 발생합니다.
     * - set - 다른 아이템으로 변경
     * - add - 아이템 추가
     * - remove - 아이템 삭제
     * - move - 아이템 이동
     * - update - 아이템 특정 필드 값 변경
     * - reset - 컬렉션 소스 변경
     * - refresh - 컬렉션 필터등 재 갱신
     */
    'collection-change': (event: ArrayListEvent<T>) => void;
    /**
     * 커서 인덱스가 변경되었을때 발생합니다.
     */
    'caret-change': (event: CustomEvent<{value: number; oldValue: number}>) => void;
    /**
     * 스크롤이 변경되었을때 발생합니다.
     */
    scroll: (event: CustomEvent<{oldLeft: number; oldTop: number; scrollLeft: number; scrollTop: number; trigger: Event}>) => void;
    /**
     * 아이템에 마우스 다운했을때 발생합니다.
     */
    'item-down': (event: ItemEvent<T>) => void;
    /**
     * 아이템에 마우스 업 했을때 발생합니다.
     */
    'item-up': (event: ItemEvent<T>) => void;
    /**
     * 아이템에 마우스 클릭 했을때 발생합니다.
     */
    'item-click': (event: ItemEvent<T>) => void;
    /**
     * 드래그 준비 되었을때 발생합니다.
     * 드래그를 시작하려는 요소를 사용자가 처음 클릭했을 때 발생합니다.
     */
    'item-drag-ready': (event: ListItemDragEvent<T>) => void;
    /**
     * 드래그 시작 되었을때 발생합니다.
     */
    'item-drag-start': (event: ListItemDragEvent<T>) => void;
    /**
     * 드래그 중일 때, 움직임이 감지될 때마다 발생합니다.
     */
    'item-drag-move': (event: ListItemDragEvent<T>) => void;
    /**
     * 드래그 중인 요소가 특정 영역 위에 이동했을 때 발생합니다.
     */
    'item-drag-over': (event: ListItemDragEvent<T>) => void;
    /**
     * 드래그 중인 요소가 특정 영역에서 벗어 났을 때 발생합니다.
     */
    'item-drag-out': (event: ListItemDragEvent<T>) => void;
    /**
     * 드래그 중인 요소가 드랍 가능한 영역 들어 갔을 때 발생합니다.
     */
    'item-drag-enter': (event: ListItemDragEvent<T>) => void;
    /**
     * 드래그 중인 요소가 드랍 가능 영역에서 밖으로 나갔을 때 발생합니다.
     */
    'item-drag-leave': (event: ListItemDragEvent<T>) => void;
    /**
     * 드래그 종료되었을 때 발생합니다.
     */
    'item-drag-end': (event: ListItemDragEvent<T>) => void;
    /**
     * 드래그 취소되었을 때 발생합니다.
     */
    'item-drag-cancel': (event: ListItemDragEvent<T>) => void;
    /**
     * 드래그 중인 요소가 드랍되었을때 발생합니다.
     */
    'item-drop': (event: ListItemDragEvent<T>) => void;
};

export declare type ItemState = ItemStateBase & {label: string};

export declare type ListSlot = {
    /**
     * List 기본 슬롯
     */
    default?: ItemState;
};

export interface ListProvider<T = any> {
    readonly list: List;
    itemToLabel(item: T): string;
}

export const NativeItemDragEvents = [
    'item-drag-ready',
    'item-drag-start',
    'item-drag-move',
    'item-drag-over',
    'item-drag-out',
    'item-drag-enter',
    'item-drag-leave',
    'item-drag-end',
    'item-drag-cancel',
    'item-drop'
];

export const VxListSymbol: InjectionKey<ListProvider> = Symbol.for('ListInstance');
export const NativeListEvents: Array<string> = [...NativeSelectableEvents, ...NativeItemDragEvents, 'caret-change', 'item-down', 'item-up', 'item-click', 'scroll'];

export const defaultListProps = {
    /**
     * 표시되는 아이템의 배열입니다.
     */
    items: {
        type: Array as PropType<Array<ItemResult>>
    },
    /**
     * 표시할 아이템의 필드 이름를 정의합니다.
     */
    labelField: {
        type: String as PropType<string>,
        default: 'label'
    },
    /**
     * 아이템 배열에 표시되는 최대 아이템 개수입니다.
     */
    rowCount: {
        type: Number as PropType<number>,
        default: 0
    },
    /**
     * 외부 스크롤을 정의합니다. 아이템 배열 포함하는 부모노드만 가능합니다.
     */
    scrollTarget: {
        type: [String, HTMLElement] as PropType<string | HTMLElement>,
        validator: (v: string | HTMLElement): boolean => !!v
    },

    /**
     * List 아이템이 드래그 가능한지 여부를 나타냅니다.
     */
    draggable: {
        type: Boolean as PropType<boolean>,
        default: false
    },

    /**
     * List 내부의 아이템 또는 다른 List 아이템이 해당 List 위에 드롭 가능한지 여부를 나타냅니다.
     */
    droppable: {
        type: Boolean as PropType<boolean>,
        default: false
    },
    /**
     * `items` 대한 깊은 감시를 활성화합니다.
     * 이를 통해 내부 상태 변화를 감지하고, 이에 따라 네이티브 컴포넌트의 상태를 갱신합니다.
     * 성능에 영향을 줄 수 있으므로 변화 감시가 필요한 경우에만 설정하길 권장합니다.
     */
    itemsDeep: {
        type: Boolean as PropType<boolean>,
        default: false
    },
    /**
     * 아이템 드래그시 보여주는 메시지를 호출하는 함수를 정의합니다.
     */
    hookDragMessage: {
        type: Function as PropType<(data: ListItemDragEventData) => string>
    }
};

export declare type ListPropsType = typeof defaultSelectableProps & typeof defaultListProps;

const VxListImpl = defineComponent({
    name: 'VxList',
    emits: [...NativeListEvents, 'update:modelValue', 'update:selectedItem', 'update:selectedItems', 'update:selectedValue'],
    props: {
        ...defaultSelectableProps,
        ...defaultListProps
    },
    setup(props): ListGetters & ListMethods {
        const instance = getCurrentInstance();
        const nativeInstance = new List() as NativeInstance<List>;
        const itemFactory = ComponentWrapper.factory(instance as any, VxListItem as any, ItemTemplateWrapper);

        bindNative(nativeInstance, props);
        mountNative(instance?.proxy as any, nativeInstance, {
            events: NativeListEvents,
            options: {
                ...props,
                ...useItemTemplate(nativeInstance, itemFactory)
            }
        });
        //v-model 적용
        useSelectableModels(props, nativeInstance);
        useRender(() => h('div', {class: 'x-list-wrap'}));
        provide(VxListSymbol, {
            list: nativeInstance,
            itemToLabel: item => {
                if (item == null || typeof item !== 'object') {
                    return item;
                }
                return util.getValue(item, props.labelField);
            }
        });

        return {
            nativeInstance,
            get collection(): ArrayList {
                return nativeInstance.collection as ArrayList;
            },
            scrollToIndex(index: number): void {
                nativeInstance.scrollToIndex(index);
            },
            scrollToItem(item: ItemResult) {
                nativeInstance.scrollToItem(item);
            },
            invalidate(): void {
                nativeInstance.invalidate();
            },
            flush(): void {
                nativeInstance.invalidate();
            }
        };
    }
});

const VxList = VxListImpl as unknown as DefineComponent<
    ListPropsType,
    {},
    ListGetters,
    {},
    ListMethods,
    {},
    {},
    ListEvents,
    string,
    PublicProps,
    ExtractPropTypes<ListPropsType>,
    ExtractPropTypes<ListPropsType>,
    SlotsType<ListSlot>
>;
export default VxList;
