import {defineComponent, h, reactive} from 'vue';
import {DateChooser, DateItemState} from 'vrix.js';
import {makeItemProps} from '@/components/util/useItemTemplate';

export default defineComponent({
    name: 'VxDateChooserItem',
    props: {
        ...makeItemProps<DateChooser, DateItemState>()
    },
    setup(props) {
        const state = reactive<DateItemState>(props.initState);
        return {
            state
        };
    },
    methods: {
        genContent() {
            const children = [];
            if (this.$slots.default && this.state.item) {
                children.push(this.$slots.default(this.state));
            }
            return children;
        }
    },
    render() {
        return h('div', this.genContent());
    },
    prepare(state: DateItemState): void {
        for (const key in state) {
            if (key !== 'element') {
                this.state[key] = state[key];
            }
        }
    }
});
