import {computed, defineComponent, h, inject, ref} from 'vue';
import {List} from 'vrix.js';
import {makeItemProps} from '@/components/util/useItemTemplate';
import {useRender} from '@/components/util/render';
import {ItemState, VxListSymbol} from '@/components/VxList/VxList';

export default defineComponent({
    name: 'VxListItem',
    props: {
        ...makeItemProps<List, ItemState>()
    },
    setup(props, context) {
        const provider = inject(VxListSymbol);
        const label = computed(() => provider.itemToLabel(state.value.item));
        const state = ref<ItemState>(props.initState);
        useRender(() => {
            const children = [];
            if (context.slots.default && state.value) {
                children.push(context.slots.default({...state.value, label: label.value}));
            }
            return h('div', children);
        });
        return {
            state
        };
    },
    prepare(state: ItemState): void {
        this.state = state;
    }
});
