import {DefineComponent, defineComponent, ExtractPropTypes, getCurrentInstance, h, InjectionKey, MethodOptions, nextTick, onMounted, PropType, provide, PublicProps, ref, SlotsType, watch} from 'vue';
import {AnchorAlign, Callback, Popup, AnchorTrigger} from 'vrix.js';
import {bindNative, mountNative, NativeInstance} from '../mixins/NativeComponent';
import {useModel} from '@/components/util/useModel';
import {useRender} from '@/components/util/render';

declare type PopupGetters = {
    /**
     * `VxPopup` 감싸고 있는  Popup 인스턴스입니다.
     */
    nativeInstance: Popup;
    /**
     * 팝업이 열렸는지 닫혔는지를 나타냅니다.
     */
    get isOpened(): boolean;
};

declare interface PopupMethods extends MethodOptions {
    [key: string]: any;
    /**
     * 팝업을 엽니다.
     * @param data `opening`과 `opened` 콜백 함수의 인자로 사용됩니다.
     */
    open(data?: any): PopupMethods;
    /**
     * 팝업을 닫습니다.
     * @param reason `closing`과 `closed` 콜백 함수의 인자로 사용됩니다.
     */
    close(reason?: any): PopupMethods;
    /**
     * 팝업이 열리기 전에 호출됩니다.
     */
    opening(callback: (content: HTMLElement, data: any) => void): PopupMethods;
    /**
     * 팝업이 완전히 열린 후에 호출됩니다.
     * 팝업이 열릴 때, `open` 메소드에 입력된 데이터는 콜백 함수의 인자가 됩니다.
     */
    opened(callback: (content: HTMLElement, data: any) => void): PopupMethods;
    /**
     * 팝업이 닫히기 직전에 호출됩니다.
     * 팝업이 닫힐 때, `close` 메소드에 입력된 데이터는 `reason` 값이 됩니다.
     */
    closing(callback: (reason: any) => void): PopupMethods;
    /**
     * 팝업이 닫히는 인스턴스를 처리합니다.
     * 팝업이 닫길 때, `close` 메소드에 입력된 데이터가 `reason` 값이 됩니다.
     */
    closed(callback: (reason: any) => void): PopupMethods;
    /**
     * 팝업의 열림/닫힘 상태를 전환(toggle)합니다.
     */
    toggle(data?: any): PopupMethods;
    /**
     * 팝업의 위치를 갱신합니다.
     */
    invalidate(): void;
    /**
     * 이 인스턴스와 관련된 모든 구성요소를 제거합니다.
     */
    destroy(): void;
}

export declare type PopupEvents = {
    /**
     * 팝업이 열릴 때 호출됩니다.
     */
    open: (event: Event) => void;
    /**
     * 팝업이 닫힐 때 호출됩니다.
     */
    close: (event: Event) => void;
    /**
     * 팝업 열기를 취소할 때 호출됩니다.
     */
    'cancel-open': (event: Event) => void;
    /**
     * 팝업 닫기를 취소할 때 호출됩니다.
     */
    'cancel-close': (event: Event) => void;
    /**
     * 팝업의 열림/닫힘 상태가 변경될 때 호출됩니다.
     */
    'update:modelValue': (isOpened: boolean) => void;
};

export declare type PopupSlot = {
    /**
     * Popup 기본 슬롯
     */
    default?: void;
};

export const VxPopupSymbol: InjectionKey<Popup> = Symbol.for('PopupInstance');
export const NativePopupEvents = ['open', 'close', 'cancel-open', 'cancel-close'];
export const defaultPopupProps = {
    /**
     * 팝업의 상태를(열림/닫힘) 나타냅니다.
     * @type {boolean}
     * @default false
     */
    modelValue: {
        type: Boolean as PropType<boolean>,
        default: false
    },
    /**
     * 모달 팝업 표시 여부입니다.
     * @type {boolean}
     * @default false
     */
    modal: {
        type: Boolean as PropType<boolean>,
        default: false
    },
    /**
     * content 기준으로 정렬할 방향을 지정합니다.
     * 가능한 값은 'left top', 'left center', 'left bottom', 'right top', 'right center', 'right bottom', 'center', 'center' 입니다.
     * @type {AnchorAlign}
     */
    contentAlign: {
        type: String as PropType<AnchorAlign>
    },

    /**
     * 팝업의 좌표 시스템을 설정합니다.
     * - 'global' 팝업을 전역 좌표계에 배치합니다.
     * - 'relative' 팝업을 상대적인 위치에 배치합니다('anchor' 속성이 지정되어 있을때만 가능합니다.).
     * - 'none' 팝업에 특정 좌표 시스템을 적용하지 않습니다.
     * @type {'global' | 'relative' | 'none'}
     * @default relative
     */
    coordinateMode: {
        type: String as PropType<'global' | 'relative' | 'none'>
    },
    /**
     * 팝업이 열릴 상대적 위치(HTMLElement, DomSelector)를 지정합니다.
     * 설정하지 않으면 기본적으로 최상위 노드(body)가 됩니다.
     */
    anchor: {
        type: [String, HTMLElement] as PropType<string | HTMLElement>
    },
    /**
     * anchor 기준으로 정렬할 방향을 지정합니다.
     * 가능한 값은 'left top', 'left center', 'left bottom', 'right top', 'right center', 'right bottom', 'center' 입니다
     * @type {AnchorAlign}
     */
    anchorAlign: {
        type: String as PropType<AnchorAlign>
    },
    /**
     * 해당 크기 만큼 팝업 X 위치가 변경됩니다. 단위는 픽셀입니다.
     * @type {number}
     * default: 0
     */
    offsetX: {
        type: Number as PropType<number>,
        default: 0
    },
    /**
     * 해당 크기만큼 팝업 Y 위치가 변경됩니다. 단위는 픽셀입니다.
     * @type {number}
     * @default 0
     */
    offsetY: {
        type: Number as PropType<number>,
        default: 0
    },
    /**
     * 컨텐츠가 뷰포트 바깥 영역으로 나감을 방지합니다.
     * 만일 화면크기가 뷰표트 크기보다 클땐 왼쪽상단을 기준으로 맞춥니다.
     * @default false
     */
    lockOutside: {
        type: Boolean as PropType<boolean>,
        default: false
    },
    /**
     * z-index 항상 최상위 위치에 팝업을 배치합니다.
     * @default false
     */
    alwaysOnTop: {
        type: Boolean as PropType<boolean>,
        default: false
    },
    /**
     * 다중 팝업이 열려있을때  z-index 우선순위를 정의합니다. 큰 수일수록 최상대에 배치합니다.
     * @type {number}
     * @default 0
     */
    priority: {
        type: Number as PropType<number>,
        default: 0
    },
    /**
     * 중첩된 팝업 구조를 관리합니다.
     * - 일반적인 경우, 팝업이 닫히면 연관된(자식) 팝업도 함께 닫힙니다.
     * - 단, `coordinateMode` 값이 `relative` 인 경우에는 값이 `false`여도 해당 팝업은 중첩된 팝업으로 처리됩니다.
     * @default true
     */
    nest: {
        type: Boolean as PropType<boolean>,
        default: true
    },
    /**
     * 팝업 열릴때 자동으로 팝업 컨텐츠에 포커스를 설정합니다.
     * @default true
     */
    autoFocus: {
        type: Boolean as PropType<boolean>,
        default: true
    },
    /**
     * 외부 영역 클릭시 자동 닫기 방지위한 유효영역 설정합니다.
     */
    hitAreas: {
        type: Array as PropType<Array<HTMLElement>>,
        default: () => []
    },
    /**
     * 외부영역 다운시 팝업을 닫습니다.
     * @default false
     */
    closeOnOutsideDown: {
        type: Boolean as PropType<boolean>,
        default: false
    },
    /**
     * ESC 입력시 팝업을 닫습니다.
     * @default false
     */
    closeOnEscape: {
        type: Boolean as PropType<boolean>,
        default: false
    },
    /**
     * 팝업 컨텐츠 영역에 정의된 이벤트 발생시 팝업을 닫습니다.
     * @default ['close']
     */
    closeOnEvents: {
        type: Array as PropType<Array<string>>,
        default: ['close']
    },
    /**
     * 팝업 열릴때 지연 시간(ms)입니다.
     */
    openDelay: {
        type: Number as PropType<number>,
        default: 0
    },
    /**
     * 팝업 닫힐때 지연 시간(ms)입니다.
     */
    closeDelay: {
        type: Number as PropType<number>,
        default: 0
    },
    /**
     * 앵커 엘리먼트의 'click' 또는 'over' 이벤트 발생시 자동으로 팝업 엽니다.
     */
    openOnTrigger: {
        type: String as PropType<AnchorTrigger>
    },
    /**
     * 렌더 타이밍, 'true' 이면 팝업 열리기전에 렌더링 합니다.
     * @internal 사용하지 마세요
     */
    usePreRender: {
        type: Boolean as PropType<boolean>,
        default: false
    }
};

export type PopupProps = ExtractPropTypes<{}> & typeof defaultPopupProps;

const VxPopupImpl = defineComponent({
    name: 'VxPopup',
    emits: [...NativePopupEvents, 'update:modelValue'],
    props: {
        ...defaultPopupProps
    },
    setup(props, context) {
        const instance = getCurrentInstance();
        const nativeInstance = new Popup() as NativeInstance<Popup>;
        const isFirst = ref(false);

        if (!props.usePreRender) {
            const unwatch = watch(
                () => props.modelValue,
                value => {
                    if (value) {
                        isFirst.value = true;
                        nextTick().then(() => unwatch());
                    }
                },
                {immediate: true}
            );
        }

        useModel(props, 'modelValue', nativeInstance, 'isOpened', NativePopupEvents);
        bindNative(nativeInstance, props);
        mountNative(instance.proxy, nativeInstance, {
            events: NativePopupEvents,
            options: {
                ...props,
                content: new Promise<HTMLElement>((resolve, reject) => {
                    const child = () => instance.proxy.$el.firstElementChild;
                    onMounted(() => {
                        if (props.usePreRender || isFirst.value) {
                            resolve(child());
                        } else {
                            const unwatch = watch(
                                isFirst,
                                value => {
                                    if (value) {
                                        if (!child()) {
                                            nextTick().then(() => {
                                                if (!child()) {
                                                    reject('Failed to generate content in the expected timeframe. ');
                                                } else {
                                                    resolve(child());
                                                }
                                            });
                                        } else {
                                            resolve(child());
                                        }
                                        unwatch();
                                    }
                                },
                                {immediate: true, flush: 'post'}
                            );
                        }
                    });
                })
            }
        });

        provide(VxPopupSymbol, nativeInstance);
        useRender(() => {
            if (!props.usePreRender && !isFirst.value) {
                return h('template');
            }
            return h('template', context.slots.default());
        });

        nativeInstance.addEventListener('trigger-open', () => {
            isFirst.value = true;
        });

        function opening(callback: Callback) {
            nativeInstance.opening(callback);
            return this;
        }

        function opened(callback: Callback) {
            nativeInstance.opened(callback);
            return this;
        }

        function closing(callback: Callback) {
            nativeInstance.closing(callback);
            return this;
        }

        function closed(callback: Callback) {
            nativeInstance.closed(callback);
            return this;
        }

        function open(data?: any) {
            isFirst.value = true;
            nativeInstance.open(data);
            return this;
        }

        function close(data?: any) {
            nativeInstance.close(data);
            return this;
        }

        function toggle(data?: any) {
            return nativeInstance.isOpened ? close(data) : open(data);
        }

        function invalidate(): void {
            nativeInstance.invalidate();
        }

        function destroy(): void {
            nativeInstance.destroy();
        }

        return {
            nativeInstance,
            get isOpened() {
                return nativeInstance.isOpened;
            },
            opening,
            opened,
            closing,
            closed,
            open,
            close,
            toggle,
            invalidate,
            destroy
        };
    }
});

const VxPopup = VxPopupImpl as unknown as DefineComponent<
    PopupProps,
    {},
    PopupGetters,
    {},
    PopupMethods,
    {},
    {},
    PopupEvents,
    string,
    PublicProps,
    ExtractPropTypes<PopupProps>,
    ExtractPropTypes<PopupProps>,
    SlotsType<PopupSlot>
>;

export default VxPopup;
