import {
    ComponentInternalInstance,
    DefineComponent,
    defineComponent,
    ExtractPropTypes,
    getCurrentInstance,
    h,
    InjectionKey,
    isProxy,
    MethodOptions,
    onMounted,
    PropType,
    provide,
    PublicProps,
    reactive,
    Ref,
    SlotsType
} from 'vue';
import {Autocomplete, ItemState, SuggestEvent, CoordinateMode} from 'vrix.js';
import ComponentWrapper from '@/components/util/ComponentWrapper';
import {bindNative, mountNative, NativeInstance} from '../mixins/NativeComponent';
import ItemTemplateWrapper from '@/components/util/ItemTemplateWrapper';
import VxSuggestItem from '@/components/VxAutocomplete/VxSuggestItem';
import useItemTemplate from '@/components/util/useItemTemplate';
import {useModel} from '@/components/util/useModel';
import {ItemResult, NativeSelectableEvents, SelectionEvents} from '../mixins/Selectable';
import VxSuggestStatus from '@/components/VxAutocomplete/VxSuggestStatus';
import {useRender} from '@/components/util/render';

export declare type AutocompleteGetters = {
    /**
     * `VxAutocomplete` 감싸고 있는 Autocomplete 인스턴스입니다.
     */
    nativeInstance: Autocomplete;
};

export declare interface AutocompleteMethods extends MethodOptions {
    [key: string]: any;
    /**
     * 입력 필드에 포커스를 설정합니다.
     */
    focus(): void;
    /**
     * 입력 필드에 포커스를 해제합니다.
     */
    blur(): void;
    /**
     * 제안 목록을 엽니다. 해당 파라메터 `value` 와 `suggestSource` 속성을 이용해여 제안 목록을 가져오고 표시합니다.
     * 만약 'value' 값이 명시적으로 주어지지 않은 경우, 현재 입력 필드의 값이 사용됩니다.
     * @param {string} value
     * @returns {boolean} - 제안 목록이 성공적으로 열렸는지 여부를 나타냅니다.
     */
    openSuggest(value?: string): boolean;
    /**
     * 제안 목록을 닫습니다.
     */
    closeSuggest(): void;
    /**
     * 입력된 값, 표시된 제안 목록, 그리고 선택된 아이템을 초기화합니다.
     */
    clear(): void;
}

export declare type AutocompleteEvents<T = any> = SelectionEvents<T> & {
    /**
     * 제안목록에서 커서 인덱스가 변경되었을때 발생합니다.
     */
    'suggest-caret-change': (event: CustomEvent<{value: number; oldValue: number}>) => void;
    /**
     * 제안목록이 열리기 전에 발생합니다.
     */
    'suggest-opening': (event: SuggestEvent<T>) => void;
    /**
     * 제안목록이 열릴때 발생합니다.
     */
    'suggest-open': (event: SuggestEvent<T>) => void;
    /**
     * 제안목록이 닫히기 전에 발생합니다.
     */
    'suggest-closing': (event: SuggestEvent<T>) => void;
    /**
     * 제안목록이 닫힐때 발생합니다.
     */
    'suggest-close': (event: SuggestEvent<T>) => void;
    /**
     * 제안 목록의 특정 아이템을 선택했을 때 발생합니다.
     */
    'suggest-select': (event: SuggestEvent<T>) => void;
    /**
     * 'value' 프로버티가 변경될때 발생합니다.
     */
    'value-change': (event: CustomEvent<{value: string; oldValue: string}>) => void;
    /**
     * input 요소의 value 변경될때 발생합니다.
     */
    input: (event: CustomEvent<{value: string; trigger: InputEvent}>) => void;
    /**
     * input 요소에서 키보드를 누를때 발생합니다.
     */
    keydown: (event: CustomEvent<{value: string; trigger: KeyboardEvent}>) => void;
    /**
     * input 요소에서 키보드를 땔때 발생합니다.
     */
    keyup: (event: CustomEvent<{value: string; trigger: KeyboardEvent}>) => void;
    /**
     * 포커스를 받을때 발생합니다
     */
    focusin: (event: CustomEvent<{trigger: Event}>) => void;
    /**
     * 포커스를 잃을때 발생합니다.
     */
    focusout: (event: CustomEvent<{trigger: Event}>) => void;
};

export declare type AutocompleteSlot = {
    /**
     * 제안 목록 슬롯
     */
    suggest?: ItemState & {label: string; text: string; html: string};
    /**
     * 로딩 슬롯
     */
    progress?: string;
    /**
     * 데이타 없음 슬롯
     */
    nodata?: string;
};

export interface AutocompleteProvider {
    readonly instance: Autocomplete;
    get inputValue(): Ref<string>;
}

export const VxAutocompleteSymbol: InjectionKey<AutocompleteProvider> = Symbol.for('AutocompleteInstance');

export const NativeAutocompleteEvents: Array<string> = [
    ...NativeSelectableEvents,
    'suggest-opening',
    'suggest-open',
    'suggest-closing',
    'suggest-close',
    'suggest-select',
    'suggest-caret-change',
    'value-change',
    'input',
    'keydown',
    'keyup',
    'focusin',
    'focusout'
];
export const defaultInputProps = {
    /**
     * 입력 필드의 레이블 텍스트입니다.
     */
    label: {
        type: String as PropType<string>,
        default: ''
    },
    /**
     * 입력 필드에 값이 입력되지 않는 경우 표시되는 텍스트입니다.
     */
    placeholder: {
        type: String as PropType<string>
    },
    /**
     * 표시될 아이템의 값이 결정되는 필드를 정의합니다.
     * 이 값이 문자열일 경우, 아이템 객체의 해당 키의 값(item[labelField])이 표시되고
     * 콜백일 경우 (item: T) => string 반환된 문자열이 값이 표시됩니다.
     */
    labelField: {
        type: [String, Function] as PropType<string | ((item) => string)>,
        default: null
    },
    /**
     * 아이템 선택시 자동으로 제안 목록 닫을지를 정의합니다. 기본값음 true 입니다.
     * @defaultValue true
     */
    closeOnSelect: {
        type: Boolean as PropType<boolean>,
        default: true
    },
    /**
     * 포커스 아웃시 자동으로 제안 목록 닫을지를 정의합니다. 기본값은 true 입니다.
     * @defaultValue true
     */
    closeOnFocusOut: {
        type: Boolean as PropType<boolean>,
        default: true
    },
    /**
     * 외부영역 다운시 자동으로 제안 목록 닫을지를 정의합니다. 기본값은 true 입니다.
     * @defaultValue true
     */
    closeOnOutsideDown: {
        type: Boolean as PropType<boolean>,
        default: true
    },
    /**
     * 입력 필드에 현재 입력된 값입니다.
     */
    inputValue: {
        type: String as PropType<String>,
        default: ''
    },
    /**
     * 최소 문자 입력 개수입니다. 최소 개수 만족시 제안목록이 활성화 합니다.
     */
    minChar: {
        type: Number as PropType<number>,
        default: 0
    },
    /**
     * 입력필드에 값이 있을때 초기화 버튼 활성화 여부입니다.
     * @defaultValue false
     */
    clearable: {
        type: Boolean as PropType<boolean>,
        default: false
    },
    /**
     * 사용자가 특정 키를 눌렀을 때 값을 입력하는 코드 배열입니다.
     * 기본 키코드는 [9, 13]으로, 이는 각각 "Tab", "Enter" 입니다.
     */
    delimiterOnKeys: {
        type: Array as PropType<Array<number>>,
        default: [9, 13]
    },
    /**
     * 팝업의 좌표 시스템을 설정합니다.
     * - 'global' 팝업을 전역 좌표계에 배치합니다.
     * - 'relative' 팝업을 상대적인 위치에 배치합니다('anchor' 속성이 지정되어 있을때만 가능합니다.).
     * - 'none' 팝업에 특정 좌표 시스템을 적용하지 않습니다.
     * 기본 값은 'relative' 입니다.
     */
    coordinateMode: {
        type: String as PropType<CoordinateMode>,
        default: 'relative'
    }
};

export const defaultSuggestProps = {
    /**
     * 키 입력 시 해당 입력 값에 일치하는 데이터를 생성하는 데이터 소스를 정의합니다.
     * Array<T>, Promise<Array<T>>, 콜백 함수 (keyword: string) => Array<T>|Promise<Array<T>> 중 하나의 형태를 가질 수 있습니다.
     * 아래의 예제는 이 콜백 함수가 어떻게 작동하는지를 보여줍니다.
     * ```typescript
     *  (keyword: string) => {
     *      return fetch(`https://api.example.com/data?keyword=${encodeURIComponent(keyword)}`, {
     *          method: 'GET'
     *      });
     *  };
     * ```
     */
    suggestSource: {
        type: [Array, Function, Promise] as PropType<string | ((item) => string) | Promise<any>>
    },
    /**
     * 제안 목록에 표시될 아이템 개수입니다.
     */
    suggestCount: {
        type: Number as PropType<number>,
        default: 6
    },
    /**
     * @private
     */
    hookSuggestItems: {
        type: Function,
        default: items => {
            if (!isProxy(items)) {
                items = reactive(items);
            }
            return items;
        }
    }
};

export const defaultAutocompleteProps = {
    /**
     * 입력필드에 입력된 값을 나타냅니다.
     */
    modelValue: {
        type: String as PropType<string>
    },
    /**
     * 제안 목록에서 선택했던 아이템입니다.
     */
    selectedItem: {
        type: [String, Number, Object] as PropType<ItemResult>
    }
};

export function makeDefaultMethods<T extends Autocomplete>(instance: NativeInstance<T>) {
    return {
        focus(): void {
            instance.focus();
        },
        blur(): void {
            instance.blur();
        },
        openSuggest(value?: string): boolean {
            return instance.openSuggest(value);
        },
        closeSuggest(): void {
            instance.closeSuggest();
        },
        clear(): void {
            instance.clear();
        }
    };
}

export declare type AutocompletePropsType = typeof defaultInputProps & typeof defaultSuggestProps & typeof defaultAutocompleteProps;

const VxAutocompleteImpl = defineComponent({
    name: 'VxAutocomplete',
    props: {
        ...defaultInputProps,
        ...defaultSuggestProps,
        ...defaultAutocompleteProps
    },
    emits: [...NativeAutocompleteEvents, 'update:modelValue', 'update:selectedItem'],
    setup(props) {
        const instance = getCurrentInstance();
        const nativeInstance = new Autocomplete();
        const suggestItemFactory = ComponentWrapper.factory(instance, VxSuggestItem as any, ItemTemplateWrapper);
        const {
            itemTemplate: suggestItemTemplate,
            hookRenderElement: hookRenderSuggestElement,
            hookFreeElement: hookFreeSuggestElement
        } = useItemTemplate(nativeInstance.suggestList, suggestItemFactory);

        bindNative(nativeInstance, props);
        mountNative(instance.proxy, nativeInstance, {
            events: NativeAutocompleteEvents,
            options: {
                ...props,
                suggestItemTemplate,
                hookRenderSuggestElement,
                hookFreeSuggestElement
            }
        });

        const inputValue = useModel(props, 'modelValue', nativeInstance, 'inputValue', 'value-change');
        useModel(props, 'selectedItem', nativeInstance, 'selectedItem', 'selection-change');
        useRender(() => h('div', {class: 'x-autocomplete-wrap'}));
        provide(VxAutocompleteSymbol, {
            instance: nativeInstance,
            get inputValue() {
                return inputValue;
            }
        });
        useStateTemplate(instance, nativeInstance);

        return {
            nativeInstance,
            ...makeDefaultMethods(nativeInstance)
        };
    }
});

export function useStateTemplate(componentInstance: ComponentInternalInstance, nativeInstance: NativeInstance<Autocomplete>) {
    const {nodata, progress} = componentInstance.slots;
    if (nodata || progress) {
        const stateFactory = ComponentWrapper.factory(componentInstance, VxSuggestStatus, ComponentWrapper);
        onMounted(() => {
            if (nodata) {
                nativeInstance.suggestNodataStateTemplate = stateFactory({slotName: 'nodata'}).element;
            }
            if (progress) {
                nativeInstance.suggestProgressStateTemplate = stateFactory({slotName: 'progress'}).element;
            }
        });
    }
}

const VxAutocomplete = VxAutocompleteImpl as unknown as DefineComponent<
    AutocompletePropsType,
    {},
    AutocompleteGetters,
    {},
    AutocompleteMethods,
    {},
    {},
    AutocompleteEvents,
    string,
    PublicProps,
    ExtractPropTypes<AutocompletePropsType>,
    ExtractPropTypes<AutocompletePropsType>,
    SlotsType<AutocompleteSlot>
>;

export default VxAutocomplete;
