import {DefineComponent, defineComponent, ExtractPropTypes, getCurrentInstance, h, InjectionKey, MethodOptions, PropType, provide, PublicProps, SlotsType} from 'vue';
import {DateChooser, DateItem, DateItemState, DateType, FormatType, ItemEvent} from 'vrix.js';
import ComponentWrapper from '@/components/util/ComponentWrapper';
import {bindNative, mountNative, NativeInstance} from '../mixins/NativeComponent';
import VxDateChooserItem from '@/components/VxDateChooser/VxDateChooserItem';
import ItemTemplateWrapper from '@/components/util/ItemTemplateWrapper';
import useItemTemplate from '@/components/util/useItemTemplate';
import {useModel} from '@/components/util/useModel';
import {SelectionEvents} from '../mixins/Selectable';

export declare type DateChooserGetters = {
    /**
     * `VxDateChooser` 감싸고 있는 DateChooser 인스턴스입니다.
     */
    nativeInstance: DateChooser;
};

export declare interface DateChooserMethods extends MethodOptions {
    [key: string]: any;
    /**
     * 선택된 날짜를 삭제합니다.
     */
    clear(): void;
    /**
     * 선택된 날짜로 이동하거나, 선택된 날짜가 없는 경우 오늘의 날짜로 이동합니다.
     */
    reset(): void;
    /**
     * 화면에 출력된 날짜 목록을 해당 날짜로 이동합니다.
     * @param date
     */
    setCurrentDate(date: Date): void;
    /**
     * 현재 표시되는 날짜 상태('day', 'month', 'year')입니다.
     */
    getState(): DateType;
}

export declare type DateChooserEvents = SelectionEvents<Date> & {
    /**
     * 달력의 상태가 변경될 때마다 발생합니다.
     */
    'state-change': (event: CustomEvent<{value: DateType; oldValue: DateType}>) => void;
    /**
     * 아이템에 마우스 다운했을때 발생합니다.
     */
    'item-down': (event: ItemEvent<DateItem>) => void;
    /**
     * 아이템에 마우스 클릭 했을때 발생합니다.
     */
    'item-click': (event: ItemEvent<DateItem>) => void;
};

export declare type DateChooserSlot = {
    /**
     * DateChooser 기본 슬롯
     */
    default?: DateItemState;
};

interface DateChooserProvider {}

export const VxDateChooserSymbol: InjectionKey<DateChooserProvider> = Symbol.for('DateChooserInstance');
export const NativeDateChooserEvents: Array<string> = ['state-change', 'item-down', 'item-click', 'selection-changing', 'selection-change'];

export const defaultDateChooserProps = {
    modelValue: {
        type: [Date, Object] as PropType<Date | [Date, Date?]>
    },
    /**
     * 'day', 'month', 'year' 표시할 지를 정의합니다.
     * 기본값은 'day' 입니다.
     */
    type: {
        type: String as PropType<'day' | 'month' | 'year'>,
        default: 'day'
    },
    /**
     * 오늘 날짜를 정의합니다.
     */
    today: {
        type: Date as PropType<Date>,
        default: new Date()
    },
    /**
     * 헤더에 표시되는 날짜의 형식입니다.
     * ```typescript
     * dateChooser.headerPatterns = {day : 'YYYY.MM', month: 'YYYY',  year:'{YYYY} ~ {YYYY}'}
     * ```
     */
    headerPatterns: {
        type: Object as PropType<FormatType>
    },
    /**
     * 표시되는 날짜의 형식입니다.
     * `DateChooser.setFormatter` 설정된 포멧터에 의해 결정됩니다.
     * ```typescript
     * dateChooser.pattern = {day : 'YYYY.MM.DD', month: 'YYYY.MM.DD',  year:'YYYY'}
     * ```
     */
    patterns: {
        type: Object as PropType<FormatType>
    },
    /**
     * 사용자가 날짜 범위를 선택할 수 있는지 여부입니다.
     */
    allowRange: {
        type: Boolean as PropType<boolean>,
        default: false
    },
    /**
     * 선택 가능한 날짜를 정의합니다.
     * ```typescript
     * dateChooser.allowDates = (date: Date) => boolean {}
     * ```
     */
    allowDates: {
        type: Function as PropType<(date: Date) => boolean>
    },

    /**
     * 선택된 날짜 범위를 정의합니다.
     * ```typescript
     * dateChooser.selectedRange = [new Date('2020/1/1'), new Date('2020/1/31')]
     * ```
     */
    selectedRange: {
        type: Array as PropType<Array<Date>>
    },
    /**
     * 선택된 날짜를 정의합니다.
     * 날짜는 Date 객체로 반환납니다.
     * ```typescript
     * dateChooser.selectedDate = new Date('2020/1/1');
     * ```
     */
    selectedDate: {
        type: Date as PropType<Date>
    },
    /**
     * 선택된 날짜를 정의합니다.
     * 문자열의 형식는 `DateChooser.setFormatter` 통해 설정된 포맷터와 'pattern' 속성에 의해 생성됩니다.
     */
    selectedDateString: {
        type: String as PropType<string>
    }
};

export type DateChooserPropsType = ExtractPropTypes<{}> & typeof defaultDateChooserProps;

const VxDateChooserImpl = defineComponent({
    name: 'VxDateChooser',
    emits: [...NativeDateChooserEvents, 'update:modelValue', 'update:selectedDate', 'update:selectedDateString', 'update:selectedRange'],
    props: {
        ...defaultDateChooserProps
    },
    setup(props) {
        const instance = getCurrentInstance();
        const nativeInstance = new DateChooser() as NativeInstance<DateChooser>;

        let options = null;
        if (instance.slots.default) {
            const itemFactory = ComponentWrapper.factory(instance, VxDateChooserItem, ItemTemplateWrapper);
            options = {...props, ...useItemTemplate(nativeInstance, itemFactory)};
        } else {
            options = props;
        }

        bindNative(nativeInstance, props);
        mountNative(instance.proxy, nativeInstance, {events: NativeDateChooserEvents, options});
        useModel(props, 'modelValue', nativeInstance, props.allowRange ? 'selectedRange' : 'selectedDate', 'selection-change');
        useModel(props, 'selectedDate', nativeInstance, 'selectedDate', 'selection-change');
        useModel(props, 'selectedDateString', nativeInstance, 'selectedDateString', 'selection-change');
        if (props.allowRange) {
            useModel(props, 'selectedRange', nativeInstance, 'selectedRange', 'selection-change');
        }

        provide(VxDateChooserSymbol, {});
        return {
            nativeInstance,
            getState(): string {
                return this.nativeInstance.state;
            },
            clear(): void {
                return this.nativeInstance.clear();
            },
            reset(): void {
                return this.nativeInstance.reset();
            },
            setCurrentDate(date: Date): void {
                return this.nativeInstance.setCurrentDate(date);
            }
        };
    },
    render() {
        return h('div', {class: 'x-date-chooser-wrap'});
    }
});

const VxDateChooser = VxDateChooserImpl as unknown as DefineComponent<
    DateChooserPropsType,
    {},
    DateChooserGetters,
    {},
    DateChooserMethods,
    {},
    {},
    DateChooserEvents,
    string,
    PublicProps,
    ExtractPropTypes<DateChooserPropsType>,
    ExtractPropTypes<DateChooserPropsType>,
    SlotsType<DateChooserSlot>
>;

export default VxDateChooser;
