import {onMounted, onUpdated} from 'vue';
import ComponentWrapper from '@/components/util/ComponentWrapper';

export default class ItemTemplateWrapper extends ComponentWrapper {
    private releaseOnUpdate;
    private releaseOnMount;

    constructor(wrapper: any) {
        super(wrapper);
    }

    onUpdated(hook: () => any): void {
        this.releaseOnUpdate = onUpdated(hook, this.instance['_']);
    }

    onMounted(hook: () => any): void {
        this.releaseOnMount = onMounted(hook, this.instance['_']);
    }

    prepare(state: Record<string, any>): void {
        this.instance['_'].type.prepare?.apply(this.instance, [state]);
    }

    destroy(): void {
        super.destroy();
        if (this.releaseOnUpdate) {
            //this.releaseOnUpdate();
        }
        if (this.releaseOnMount) {
            //this.releaseOnMount();
        }
    }
}
