import {ComponentInternalInstance, ComponentPublicInstance, createVNode, defineComponent, DefineComponent, getCurrentInstance, render, VNode} from 'vue';

export type VueDefineComponent = DefineComponent<any, any, any>;
type WrapperData = {
    vnode: VNode;
    container: HTMLElement;
};

export declare type Constructor<T = any> = new (...args: any[]) => T;
type HTMLElementEx = HTMLElement & {__wrapper: ComponentWrapper};

export default class ComponentWrapper {
    private readonly wrapper: WrapperData;

    constructor(wrapper: WrapperData) {
        this.wrapper = wrapper;
        Object.defineProperty(this.element, '__wrapper', {
            get: () => {
                return this;
            }
        });
    }

    get instance(): ComponentPublicInstance {
        return this.wrapper.vnode.component!.proxy!;
    }

    get element(): HTMLElementEx {
        return this.wrapper.vnode.el as HTMLElementEx;
    }

    destroy(): void {
        render(null, this.wrapper.container);
    }

    static factory<T extends Constructor>(host: ComponentPublicInstance | ComponentInternalInstance, type: VueDefineComponent, WrapperClass: T): (props: object) => ComponentWrapper {
        return factory(host, type, WrapperClass);
    }
}

export function factory<T extends Constructor>(host: ComponentPublicInstance | ComponentInternalInstance, type: VueDefineComponent, wrapperClass: T): () => ComponentWrapper {
    const instance = (host as ComponentPublicInstance).$ || (host as ComponentInternalInstance);
    if (type.__setup) {
        type = defineComponent({...type} as any) as any;
    }
    const hookSetup = type.__setup || type.setup;
    type.__setup = hookSetup;
    type.setup = (props, ctx) => {
        getCurrentInstance()!.parent = instance;
        if (hookSetup) {
            return hookSetup(props, ctx);
        }
    };

    const create = (args: any[]): WrapperData => {
        const data = args.length > 0 ? args[0] : {};
        const vnode: VNode = createVNode(type, data, {...instance.slots});
        const container = document.createElement('template') as HTMLElement;
        container.className = 'wrapper';
        if (instance.appContext) {
            vnode.appContext = instance.appContext;
        }
        render(vnode, container);
        return {vnode, container};
    };

    return (...args) => {
        return new wrapperClass(create(args));
    };
}
