import {defineComponent, h, inject} from 'vue';
import {useRender} from '@/components/util/render';
import {VxAutocompleteSymbol} from '@/components/VxAutocomplete/VxAutocomplete';

export default defineComponent({
    name: 'VxSuggestStatus',
    props: {
        slotName: {
            type: String
        }
    },
    setup(props, context) {
        const slot = context.slots[props.slotName];
        const provider = inject(VxAutocompleteSymbol);
        useRender(() => {
            return h('div', slot(provider.inputValue.value));
        });
    }
});
