import {PropType} from 'vue';
import {NativeInstance} from './NativeComponent';
import {SelectionEvent, IElement} from 'vrix.js';
import {useModel} from '@/components/util/useModel';

export const NativeSelectableEvents: Array<string> = ['collection-change', 'selection-changing', 'selection-change'];

export type ItemResult = string | number | {[prop: string]: any};

export type SelectionEvents<T = any> = {
    /**
     * 아이템이 선택되기 전에 발생합니다.
     * 이벤트 핸들러에서 이벤트의 `preventDefault` 메소드를 호출하여 선택 작업을 취소할 수 있습니다.
     */
    'selection-changing': (event: SelectionEvent<T>) => void;
    /**
     * 아이템이 선택된 후에 발생합니다.
     */
    'selection-change': (event: SelectionEvent<T>) => void;
};

export const defaultSelectableProps = {
    /**
     * 선택된 아이템 또는 아이템 배열을 나타냅니다.
     * `valueField` 정의 되어 있으면 선택된 아이템의 필드 값 또는 값 배열을 나타냅니다.
     */
    modelValue: {
        type: [Array, Object, Number, String] as PropType<Array<ItemResult> | ItemResult>
    },
    /**
     * 사용자가 여러 항목을 선택할 수 있는지 여부입니다.
     */
    allowMultiple: {
        type: Boolean as PropType<boolean>,
        default: false
    },
    /**
     * 적어도 하나의 아이템이 항상 선택되어야 하는 여부입니다.
     */
    requireSelection: {
        type: Boolean as PropType<boolean>,
        default: false
    },
    /**
     * 선택된 아이템의 값을 결정하는 필드의 이름입니다.
     */
    valueField: {
        type: String as PropType<string>
    },
    /**
     * 선택된 아이템을 정의합니다.
     */
    selectedItem: {
        type: [Object, Number, String] as PropType<ItemResult>
    },
    /**
     * 복수 선택된 아이템을 정의합니다.
     */
    selectedItems: {
        type: Array as PropType<Array<ItemResult>>
    },
    /**
     * 선택된 아이템의 `valueField` 속성 값을 정의합니다
     */
    selectedValue: {
        type: [Object, Number, String] as PropType<any>
    }
};

export function useSelectableModels(props: Record<string, any>, nativeInstance: NativeInstance<IElement>): void {
    const field = `selected${props.valueField != null ? 'Value' : 'Item'}${props.allowMultiple ? 's' : ''}`;
    useModel(props, 'modelValue', nativeInstance, field, 'selection-change');
    useModel(props, 'selectedItem', nativeInstance, 'selectedItem', 'selection-change');
    useModel(props, 'selectedItems', nativeInstance, 'selectedItems', 'selection-change');
    //버전 호환용
    if (props.valueField) {
        useModel(props, 'selectedValue', nativeInstance, 'selectedValue', 'selection-change');
    }
}

export declare type SelectableProps = {
    modelValue?: Array<ItemResult> | ItemResult;
    allowMultiple?: boolean;
    requireSelection?: boolean;
    valueField?: string;
    selectedItem?: ItemResult;
    selectedItems?: Array<ItemResult>;
};
