import {computed, defineComponent, h, inject, ref} from 'vue';
import {TreeItemState, VxTreeSymbol} from '@/components/VxTree/VxTree';
import {Tree} from 'vrix.js';
import {makeItemProps} from '@/components/util/useItemTemplate';
import {useRender} from '@/components/util/render';
import {ItemResult} from '@/components/mixins/Selectable';

export default defineComponent({
    name: 'VxTreeItem',
    props: {
        ...makeItemProps<Tree, TreeItemState>()
    },
    setup(props, context) {
        const provider = inject(VxTreeSymbol);
        const state = ref<TreeItemState>(props.initState);
        const label = computed(() => provider.itemToLabel(state.value.item));
        const hookToggle = (item: ItemResult): void => {
            props.nativeInstance.toggleNode(item);
        };
        useRender(() => {
            const children = [];
            if (context.slots.default && state.value) {
                children.push(context.slots.default({...state.value, label: label.value, hookToggle}));
            } else {
                const toggleNode = h(
                    'button',
                    {
                        class: 'x-toggle',
                        onMousedown: event => {
                            event.preventDefault();
                        },
                        onClick: event => {
                            event.preventDefault();
                            props.nativeInstance.toggleNode(state.value.item);
                        }
                    },
                    [h('i', {class: 'x-toggle-icon'})]
                );
                const labelNode = h(
                    'div',
                    {
                        class: 'x-label'
                    },
                    label.value
                );
                children.push(toggleNode, labelNode);
            }

            return h('div', children);
        });

        return {
            state
        };
    },
    prepare(state: TreeItemState): void {
        this.state = state;
    }
});
