import {computed, defineComponent, h, PropType} from 'vue';
import {Combobox} from 'vrix.js';
import {NativeInstance} from '../mixins/NativeComponent';
import {ItemResult} from '@/components/mixins/Selectable';

export default defineComponent({
    name: 'VxTextChip',
    props: {
        item: {
            type: [String, Number, Object] as PropType<ItemResult>
        },
        nativeInstance: {
            type: Object as PropType<NativeInstance<Combobox>>
        }
    },
    setup(props) {
        const label = computed(() => props.nativeInstance.itemToLabel(props.item));
        return {
            label
        };
    },
    methods: {
        genLabel() {
            return h('span', {class: 'x-label'}, this.label);
        },
        genContent() {
            if (this.item) {
                if (this.$slots.chip) {
                    return this.$slots.chip({
                        item: this.item,
                        label: this.label
                    });
                }
                return [this.genLabel()];
            }
            return [];
        }
    },
    render() {
        return h('div', {class: 'x-select-text'}, this.genContent());
    }
});
