import {computed, getCurrentInstance, onMounted, ref, watch} from 'vue';
import {NativeInstance} from '../mixins/NativeComponent';
import {IElement, Popup} from 'vrix.js';
export function useModel(props: Record<string, any>, prop: string, nativeInstance: NativeInstance<IElement | Popup>, nativeProp: string, changeEvent: string | Array<string>) {
    const instance = getCurrentInstance();
    const internal = ref(props[prop]);
    const isTwoWay = computed(() => {
        if (!(prop in props)) {
            return false;
        }
        const vProps = instance.vnode.props;
        return !!(vProps && prop in vProps && `onUpdate:${prop}` in vProps);
    });

    const model = computed({
        get() {
            return isTwoWay.value ? props[prop] : internal.value;
        },
        set(value) {
            if (internal.value !== value) {
                internal.value = value;
                instance?.emit(`update:${prop}`, value);
            }
        }
    });

    watch(
        model,
        value => {
            if (!instance.isMounted) {
                if (value === undefined) {
                    return;
                }
                onMounted(() => {
                    if (nativeInstance[nativeProp] != value) {
                        nativeInstance[nativeProp] = value;
                    }
                });
            } else {
                if (value === undefined) {
                    value = null;
                }
                if (nativeInstance[nativeProp] != value) {
                    nativeInstance[nativeProp] = value;
                }
            }
        },
        {immediate: true, flush: 'post'}
    );

    const onSetValue = (): void => (model.value = nativeInstance[nativeProp]);
    const events = Array.isArray(changeEvent) ? changeEvent : [changeEvent];
    events.forEach(name => {
        nativeInstance.addEventListener(name, onSetValue);
    });

    return model;
}
