import {ComponentPublicInstance, onMounted, onUnmounted, toRefs, watch, watchEffect} from 'vue';
import {IElement, IList, Popup} from 'vrix.js';

export declare type NativeInstance<T> = T & EventTarget;

const FLUSH_AFTER_MOUNTED = ['scrollTarget', 'chipTemplate', 'items', 'selectedItem', 'selectedItems', 'selectedValue', 'selectedValues'];
const DEEP_WATCH = ['items'];

export function mountNative(
    componentInstance: ComponentPublicInstance,
    nativeInstance: NativeInstance<IElement | Popup>,
    data: {
        events: Array<string>;
        options: {};
    }
) {
    const onDispatch = (event: Event): void => componentInstance.$emit(event.type, event);
    const options = Object.keys(data.options).reduce(
        (item, key) => {
            const v = data.options[key];
            if (v !== undefined) {
                const flushAfterMounted = FLUSH_AFTER_MOUNTED.indexOf(key) >= 0;
                item[flushAfterMounted ? 'post' : 'pre'][key] = v;
            }
            return item;
        },
        {pre: {}, post: {}}
    );

    data.events.forEach(type => nativeInstance.addEventListener(type, onDispatch));
    nativeInstance.setOptions(options.pre);
    onMounted(() => {
        if (!(nativeInstance instanceof Popup)) {
            nativeInstance.mount(componentInstance.$el, options.post);
        }
    });

    onUnmounted(() => {
        data.events.forEach(type => nativeInstance.removeEventListener(type, onDispatch));
        nativeInstance.destroy();
    });
}

export function bindNative<C extends object, Props extends Record<string, any>>(target: C, props: Props, postWatchProps: string[] = []): () => void {
    const refs = toRefs(props);
    const unwatchs = Object.keys(refs).map((key: string) => {
        if (postWatchProps.indexOf(key) >= 0) {
            return watchEffect(
                () => {
                    const value = refs[key].value;
                    if (target[key] != value) {
                        target[key] = value;
                    }
                },
                {
                    flush: 'post'
                }
            );
        }

        const deep = DEEP_WATCH.indexOf(key) >= 0 && props[`${key}Deep`];
        return watch(
            refs[key],
            (value, oldValue): void => {
                try {
                    if (!(key in target)) {
                        return;
                    }

                    if (target[key] != value) {
                        target[key] = value;
                    } else if (deep && 'collection' in target) {
                        (target.collection as IList<any>)?.refresh();
                    }
                } catch (e) {
                    console.warn(e);
                }
            },
            {
                deep
            }
        );
    });
    return () => {
        unwatchs.forEach(fn => fn());
    };
}
