import {DefineComponent, defineComponent, ExtractPropTypes, getCurrentInstance, h, InjectionKey, PropType, provide, PublicProps, SlotsType} from 'vue';
import {SuggestEvent, Tokenizer} from 'vrix.js';
import ComponentWrapper from '@/components/util/ComponentWrapper';
import {bindNative, mountNative, NativeInstance} from '../mixins/NativeComponent';
import {
    AutocompleteEvents,
    AutocompleteMethods,
    AutocompleteProvider,
    AutocompleteSlot,
    defaultInputProps,
    defaultSuggestProps,
    makeDefaultMethods,
    NativeAutocompleteEvents,
    useStateTemplate,
    VxAutocompleteSymbol
} from '../VxAutocomplete/VxAutocomplete';
import ItemTemplateWrapper from '@/components/util/ItemTemplateWrapper';
import VxChip from './VxChip';
import VxSuggestItem from '@/components/VxAutocomplete/VxSuggestItem';
import useItemTemplate from '@/components/util/useItemTemplate';
import {useModel} from '@/components/util/useModel';
import {defaultSelectableProps, useSelectableModels} from '../mixins/Selectable';
import {useRender} from '@/components/util/render';

export declare type TokenizerGetters = {
    /**
     * `VxTokenizer` 감싸고 있는 Tokenizer 인스턴스입니다.
     */
    nativeInstance: Tokenizer;
};

export declare interface TokenizerMethods extends AutocompleteMethods {}

export declare type TokenizerEvents<T = any> = AutocompleteEvents<T> & {
    /**
     * 토큰 아이템 추가시 발생합니다.
     */
    'suggest-add': (event: SuggestEvent<T>) => void;
    /**
     * 토큰 아이템 삭제시 발생합니다.
     */
    'suggest-remove': (event: SuggestEvent<T>) => void;
    /**
     * 토큰 아이템 추가시 중복된 아이템이 있을때 발생합니다.
     */
    'suggest-duplicate': (event: SuggestEvent<T>) => void;
};

export declare type TokenizerSlots = AutocompleteSlot & {
    /**
     * 칩 슬롯
     */
    chip?: {item: any; label: string; remove: () => void};
};

export interface TokenizerProvider extends AutocompleteProvider {}

export const VxTokenizerSymbol: InjectionKey<TokenizerProvider> = Symbol.for('TokenizerInstance');

export const NativeTokenizerEvents: Array<string> = [...NativeAutocompleteEvents, 'suggest-add', 'suggest-remove', 'suggest-duplicate'];

export const defaultTokenizerProps = {
    /**
     * 사용자는 제안 목록에서 값을 선택해서 추가하는 것 외에도,
     * 입력 필드에 직접 값을 입력하여 속성 `delimiterOnKeys` 따라 값을 추가할 수 있습니다.
     * `delimiterOnKeys`의 기본 키코드는 [9, 13]으로, 이는 각각 "Tab", "Enter" 입니다.
     */
    allowInputValue: {
        type: Boolean as PropType<boolean>,
        default: false
    }
};
export declare type TokenizerPropsType = typeof defaultInputProps & typeof defaultSuggestProps & typeof defaultSelectableProps & typeof defaultTokenizerProps;

const VxTokenizerImpl = defineComponent({
    name: 'VxTokenizer',
    emits: [...NativeTokenizerEvents, 'update:modelValue', 'update:selectedItem', 'update:selectedItems', 'update:selectedValue', 'update:inputValue'],
    props: {
        ...defaultInputProps,
        ...defaultSuggestProps,
        ...defaultSelectableProps,
        ...defaultTokenizerProps,
        /**
         * 사용자가 여러 항목을 선택할 수 있는지 여부입니다.
         *
         */
        allowMultiple: {
            type: Boolean as PropType<boolean>,
            default: true
        }
    },
    setup(props) {
        const instance = getCurrentInstance();
        const nativeInstance = new Tokenizer() as NativeInstance<Tokenizer>;
        const suggestItemFactory = ComponentWrapper.factory(instance, VxSuggestItem as any, ItemTemplateWrapper);
        const chipFactory = ComponentWrapper.factory(instance, VxChip as any, ItemTemplateWrapper);
        const {
            itemTemplate: suggestItemTemplate,
            hookRenderElement: hookRenderSuggestElement,
            hookFreeElement: hookFreeSuggestElement
        } = useItemTemplate(nativeInstance.suggestList, suggestItemFactory);

        bindNative(nativeInstance, props);
        mountNative(instance.proxy, nativeInstance, {
            events: NativeTokenizerEvents,
            options: {
                ...props,
                chipTemplate: (item: any, index: number): HTMLElement => {
                    const wrapper = chipFactory({item, nativeInstance});
                    return wrapper.element;
                },
                suggestItemTemplate,
                hookRenderSuggestElement,
                hookFreeSuggestElement
            }
        });

        const inputValue = useModel(props, 'inputValue', nativeInstance, 'inputValue', 'value-change');
        useSelectableModels(props, nativeInstance);
        useRender(() => h('div', {class: 'x-tokenizer-wrap'}));
        provide(VxAutocompleteSymbol, {
            instance: nativeInstance,
            get inputValue() {
                return inputValue;
            }
        });
        provide(VxTokenizerSymbol, {
            instance: nativeInstance,
            get inputValue() {
                return inputValue;
            }
        });
        useStateTemplate(instance, nativeInstance);

        return {
            nativeInstance,
            ...makeDefaultMethods(nativeInstance)
        };
    }
});

const VxTokenizer = VxTokenizerImpl as unknown as DefineComponent<
    TokenizerPropsType,
    {},
    TokenizerGetters,
    {},
    TokenizerMethods,
    {},
    {},
    TokenizerEvents,
    string,
    PublicProps,
    ExtractPropTypes<TokenizerPropsType>,
    ExtractPropTypes<TokenizerPropsType>,
    SlotsType<TokenizerSlots>
>;

export default VxTokenizer;
