import {h, reactive} from 'vue';
import {Autocomplete, ItemState} from 'vrix.js';
import {makeItemProps} from '@/components/util/useItemTemplate';

export default {
    name: 'VxSuggestItem',
    props: {
        ...makeItemProps<Autocomplete, ItemState>()
    },
    setup(props) {
        const state = reactive<ItemState>(props.initState);
        return {
            state
        };
    },
    methods: {
        genLabel() {
            if (this.$slots.suggest) {
                return this.$slots.suggest(this.state);
            }
            return h('span', {
                innerHTML: this.state.html
            });
        },
        genContent() {
            if (this.state.item) {
                return [this.genLabel()];
            }
            return [];
        }
    },
    render() {
        return h('div', this.genContent());
    },
    prepare(state: ItemState): void {
        for (const key in state) {
            if (key !== 'element') {
                this.state[key] = state[key];
            }
        }
    }
};
