import {getCurrentInstance, render, VNode} from 'vue';

export function vnodeToElement(vnode: VNode | VNode[]): HTMLElement {
    vnode = Array.isArray(vnode) ? vnode[0] : vnode;
    const container = document.createElement('template') as HTMLElement;
    render(vnode, container);
    return vnode.el as HTMLElement;
}

export function useRender(render: () => VNode): void {
    (getCurrentInstance() as any).render = render;
}
