import {computed, defineComponent, h, PropType, ref} from 'vue';
import {Tokenizer} from 'vrix.js';
import {NativeInstance} from '../mixins/NativeComponent';
import {ItemResult} from '../mixins/Selectable';

export declare type ChipSlotProps = {item?: ItemResult; label?: string; remove?: () => void};

export default defineComponent({
    name: 'VxChip',
    props: {
        item: {
            type: [Object, String, Number] as PropType<ItemResult>
        },
        nativeInstance: {
            type: Object as PropType<NativeInstance<Tokenizer>>
        }
    },
    setup(props) {
        const state = ref<any>({});
        const slotProps = computed(() => {
            const remove = () => props.nativeInstance.removeSelectedItem(props.item);
            return {item: props.item, label: props.nativeInstance.itemToLabel(props.item), remove};
        });
        return {
            state,
            slotProps
        };
    },
    methods: {
        genLabel() {
            return h('span', {class: 'x-label'}, this.slotProps.label);
        },
        genRemover() {
            return h('button', {
                class: 'x-remover',
                onMousedown: e => {
                    e.preventDefault();
                },
                onClick: e => {
                    e.preventDefault();
                }
            });
        },
        genContent() {
            if (this.$slots.chip) {
                return this.$slots.chip(this.slotProps);
            }
            if (this.slotProps.item) {
                return [this.genLabel(), this.genRemover()];
            }
            return [this.genRemover()];
        }
    },
    render() {
        return h('div', {class: 'x-select-chip'}, this.genContent());
    }
});
