import {DefineComponent, defineComponent, ExtractPropTypes, getCurrentInstance, h, InjectionKey, PropType, provide, PublicProps, SlotsType} from 'vue';
import {HierarchyFindFn, HierarchyList, HierarchyListEvent, Tree, TreeItemState as TreeItemStateBase, util} from 'vrix.js';
import ComponentWrapper from '@/components/util/ComponentWrapper';
import VxTreeItem from '@/components/VxTree/VxTreeItem';
import {defaultSelectableProps, ItemResult, useSelectableModels} from '../mixins/Selectable';
import {bindNative, mountNative, NativeInstance} from '../mixins/NativeComponent';
import {defaultListProps, ListEvents, ListMethods, ListProvider, NativeListEvents} from '../VxList/VxList';
import {useRender} from '@/components/util/render';
import ItemTemplateWrapper from '@/components/util/ItemTemplateWrapper';
import useItemTemplate from '@/components/util/useItemTemplate';

declare type TreeGetters = {
    /**
     * `VxTree` 감싸고 있는 Tree 인스턴스입니다.
     */
    nativeInstance: Tree;
    /**
     * 계층 구조를 가진 아이템 배열을 관리하는 `HierarchyList` 인스턴스를 반환합니다.
     */
    get collection(): HierarchyList<any>;
};

declare interface TreeMethods extends ListMethods {
    /**
     * 지정된 노드의 부모 노드를 반환합니다.
     */
    getParentNode(node: TreeItem): TreeItem;

    /**
     * 해당 노드의 조상 노드 목록을 반환합니다.
     * @param node
     * @returns {Array}
     */
    getAncestorNodes(node: TreeItem): Array<TreeItem>;

    /**
     * 해당 노드의 후손 노드 목록을 반환합니다. `null` 이면 전체 노드를 반환합니다.
     * @param node
     * @returns {Array}
     */
    getDescendantNodes(node: TreeItem): Array<TreeItem>;

    /**
     * 해당노드의 형제노드들을 반환합니다.
     * @returns {Array} 형제노드 배열입니다.
     */
    getSiblingNodes(node: TreeItem): Array<TreeItem>;

    /**
     * 지정된 노드의 자식노드들을 반환합니다.
     * @param node 부모 노드입니다.
     */
    getChildren(node: TreeItem): Array<TreeItem>;
    /**
     * 해당 노드에 자식이 있는지 여부입니다.
     * @param node
     */
    hasChildren(node: TreeItem): boolean;

    /**
     * 부모 노드에 자식 노드를 추가합니다.
     * 부모 노드가 null이면 최상위 목록에 추가합니다.
     * @param parent 부모 노드
     * @param node 추가할 자식 노드
     *
     */
    addNode(parent: TreeItem, node: TreeItem): boolean;

    /**
     * 노드의 지정된 인덱스 위치에 자식 노드를 추가합니다.
     * 부모 노드가 null이면 최상위 목록에 추가합니다.
     * @param parent 부모 노드.
     * @param node 추가할 자식 노드.
     * @param index 자식 노드 삽입 위치의 인덱스.
     */
    addNodeAt(parent: TreeItem, node: TreeItem, index: number): boolean;

    /**
     * 부모 노드에 자식 노드를 추가합니다.
     * 부모 노드가 null이면 최상위 목록에 추가합니다.
     * @param parent 부모 노드
     * @param nodes 추가할 자식 노드 목록
     *
     */
    addNodes(parent: TreeItem, nodes: Array<TreeItem>): boolean;

    /**
     * 노드의 지정된 인덱스 위치에 자식 노드목록을 추가합니다.
     * 부모 노드가 `null` 이면 최상위 목록에 추가합니다.
     * @param parent 부모 노드.
     * @param nodes 추가할 자식 노드 목록
     * @param index 자식 노드 삽입 위치의 인덱스.
     */
    addNodesAt(parent: TreeItem, nodes: Array<TreeItem>, index: number): boolean;

    /**
     * 부모 노드에서 자식 노드를 삭제합니다.
     * @param node 삭제할 자식 노드 입니다.
     */
    removeNode(node: TreeItem): boolean;

    /**
     * 해당 node가 있는지 확인합니다.
     * @param node
     * @return 있으면 true,
     */
    contains(node: TreeItem): boolean;

    /**
     * 해당 노드가 확장되어 있는지 여부입니다.
     */
    isExpandedNode(node: TreeItem): boolean;

    /**
     * 지정된 노드를 확장합니다.
     */
    expandNode(node: TreeItem): boolean;

    /**
     * 전체 노드를 확장합니다.
     */
    expandAll(): void;

    /**
     * 지정된 노드를 축소합니다.
     */
    collapseNode(node: TreeItem): boolean;

    /**
     * 전체 노드를 축소합니다.
     */
    collapseAll(): void;

    /**
     * 주어진 노드의 확장 상태를 확장 또는 축소합니다.
     * @param node
     */
    toggleNode(node: TreeItem): void;

    /**
     * 확장된 노드를 설정합니다.
     * @param nodes
     */
    setExpandedNodes(nodes: Array<TreeItem>): void;

    /**
     * 확장된 노드목록을 반환합니다.
     * @returns {Array}
     */
    getExpandedNodes(): Array<TreeItem>;

    /**
     * 지정된 노드의 레벨을 반환합니다.
     */
    getNodeLevel(node: TreeItem): number;

    /**
     * 해당 노드가 가지노드(자식 노드를 가진 노드)인지 확인합니다.
     * @param node
     */
    isBranchNode(node: TreeItem): boolean;

    /**
     * 주어진 노드가 마지막노드(자식 노드를 가지지 않는 노드)인지 확인합니다.
     * @param node
     */
    isLeafNode(node: TreeItem): boolean;

    /**
     * 전체 노드를 탐색합니다.
     * 지정된 함수(callBack)에 대해 `true` 반환하는 모든 항목이 포함된 새 배열을 만듭니다
     * @param callback  function callback(node, parent, children)
     * @param prefetchNode 시작 기준 노드입니다. 지정되지 않으면 루트노드를 기준으로 합니다.
     * @param postOrder true 이면 후위탐색, false 이면 전위탐색
     * @return true 반환하는 항목의 배열입니다.
     */
    find(callback: HierarchyFindFn<TreeItem>, prefetchNode?: TreeItem, postOrder?: boolean): Array<TreeItem>;

    /**
     * 전체 노드를 탐색합니다.
     * 지정된 함수(callBack)에 대해 처음으로 `true` 반환하는 항목을 반환하고 탐색을 중지합니다.
     * @param callback  function callback(node, parent, children)
     * @param prefetchNode 시작 기준 노드입니다. 지정되지 않으면 루트노드를 기준으로 합니다.
     * @param postOrder true 이면 후위탐색, false 이면 전위탐색
     * @return 처음으로 true 반환하는 아이템입니다.
     */
    findOne(callback: HierarchyFindFn<TreeItem>, prefetchNode?: TreeItem, postOrder?: boolean): TreeItem;
}

export declare type TreeEvents<T = any> = ListEvents<T> & {
    /**
     * 컬렉션 내부의 노드 변경되었을 때  발생합니다.
     * - collapsing - 노드 접혀지기 전
     * - collapse - 노드 접혀진 후
     * - expanding - 노드 확장되기 전
     * - expand - 노드 확장된 후
     * - add - 노드 추가
     * - remove - 노드 삭제
     * - move - 노드 이동
     */
    'node-change': (event: HierarchyListEvent<T>) => void;
};

export declare type TreeItemState = TreeItemStateBase & {
    label: string;
    /**
     * 해당노드를 쉽게 확장/축소을 변경하기 위한 훅입니다.
     * @param item
     */
    hookToggle: (item: ItemResult) => void;
};

export declare type TreeSlot = {
    /**
     * Tree 기본 슬롯
     */
    default?: TreeItemState;
};

export interface TreeProvider<TreeItem = any> extends ListProvider<TreeItem> {
    readonly list: Tree;
}
export const VxTreeSymbol: InjectionKey<TreeProvider> = Symbol.for('TreeInstance');
export const NativeTreeEvents: Array<string> = [...NativeListEvents, 'node-change'];

type TreeItem = {
    [key: string]: any;
    children?: Array<TreeItem>;
};

export const defaultTreeProps = {
    /**
     * 트리 구조에서 아이템의 자식 목록을 포함하는 필드의 이름을 정의합니다.
     */
    childrenField: {
        type: String as PropType<string>,
        default: 'children'
    },
    /**
     * `items`가 갱신될 때 자동으로 확장될 아이템의 레벨을 지정합니다.
     * 예를 들어, 이 속성의 값이 2인 경우, `items`가 갱신될 때 2번째 레벨까지 모든 아이템이 자동으로 확장됩니다.
     */
    autoExpandLevel: {
        type: Number as PropType<number>,
        default: 0
    }
};

export declare type TreePropsType = typeof defaultSelectableProps & typeof defaultListProps & typeof defaultTreeProps;

const VxTreeImpl = defineComponent({
    name: 'VxTree',
    emits: [...NativeTreeEvents, 'update:modelValue', 'update:selectedItem', 'update:selectedItems', 'update:selectedValue'],
    props: {
        ...defaultSelectableProps,
        ...defaultListProps,
        ...defaultTreeProps
    },
    setup(props): TreeGetters & TreeMethods {
        const instance = getCurrentInstance();
        const nativeInstance = new Tree() as NativeInstance<Tree>;
        const itemFactory = ComponentWrapper.factory(instance, VxTreeItem as any, ItemTemplateWrapper);

        bindNative(nativeInstance, props);
        mountNative(instance.proxy, nativeInstance, {
            events: NativeTreeEvents,
            options: {
                ...props,
                ...useItemTemplate(nativeInstance, itemFactory)
            }
        });
        useSelectableModels(props, nativeInstance);
        useRender(() => h('div', {class: 'x-tree-wrap'}));
        provide(VxTreeSymbol, {
            list: nativeInstance,
            itemToLabel: item => {
                if (item == null || typeof item !== 'object') {
                    return item;
                }
                return util.getValue(item, props.labelField);
            }
        });

        return {
            nativeInstance,
            get collection(): HierarchyList<any> {
                return nativeInstance.collection as HierarchyList<any>;
            },
            scrollToIndex(index: number): void {
                nativeInstance.scrollToIndex(index);
            },
            scrollToItem(item) {
                nativeInstance.scrollToItem(item);
            },
            invalidate(): void {
                nativeInstance.invalidate();
            },
            flush(): void {
                nativeInstance.invalidate();
            },
            getParentNode(node: TreeItem): TreeItem {
                return nativeInstance.getParentNode(node);
            },
            getAncestorNodes(node: TreeItem): Array<TreeItem> {
                return nativeInstance.getAncestorNodes(node);
            },
            getDescendantNodes(node: TreeItem): Array<TreeItem> {
                return nativeInstance.getDescendantNodes(node);
            },
            getSiblingNodes(node: TreeItem): Array<TreeItem> {
                return nativeInstance.getSiblingNodes(node);
            },
            getChildren(node: TreeItem): Array<TreeItem> {
                return nativeInstance.getChildren(node);
            },
            hasChildren(node: TreeItem): boolean {
                return nativeInstance.hasChildren(node);
            },
            addNode(parent: TreeItem, node: TreeItem): boolean {
                return nativeInstance.addNode(parent, node);
            },
            addNodeAt(parent: TreeItem, node: TreeItem, index: number): boolean {
                return nativeInstance.addNodeAt(parent, node, index);
            },
            addNodes(parent: TreeItem, nodes: Array<TreeItem>): boolean {
                return nativeInstance.addNodes(parent, nodes);
            },
            addNodesAt(parent: TreeItem, nodes: Array<TreeItem>, index: number): boolean {
                return nativeInstance.addNodesAt(parent, nodes, index);
            },
            removeNode(node: TreeItem): boolean {
                return nativeInstance.removeNode(node);
            },
            contains(node: TreeItem): boolean {
                return nativeInstance.contains(node);
            },
            isExpandedNode(node: TreeItem): boolean {
                return nativeInstance.isExpandedNode(node);
            },
            expandNode(node: TreeItem): boolean {
                return nativeInstance.expandNode(node);
            },
            expandAll(): void {
                nativeInstance.expandAll();
            },
            collapseNode(node: TreeItem): boolean {
                return nativeInstance.collapseNode(node);
            },
            collapseAll(): void {
                nativeInstance.collapseAll();
            },
            toggleNode(node: TreeItem): void {
                nativeInstance.toggleNode(node);
            },
            setExpandedNodes(nodes: Array<TreeItem>): void {
                return nativeInstance.setExpandedNodes(nodes);
            },
            getExpandedNodes(): Array<TreeItem> {
                return nativeInstance.getExpandedNodes();
            },
            getNodeLevel(node: TreeItem): number {
                return nativeInstance.getNodeLevel(node);
            },
            isBranchNode(node: TreeItem): boolean {
                return nativeInstance.isBranchNode(node);
            },
            isLeafNode(node: TreeItem): boolean {
                return nativeInstance.isLeafNode(node);
            },
            find(callback: HierarchyFindFn<TreeItem>, prefetchNode: TreeItem = null, postOrder = false): Array<TreeItem> {
                return nativeInstance.find(callback, prefetchNode, postOrder);
            },
            findOne(callback: HierarchyFindFn<TreeItem>, prefetchNode: TreeItem = null, postOrder = false): TreeItem {
                return nativeInstance.findOne(callback, prefetchNode, postOrder);
            }
        };
    }
});

const VxTree = VxTreeImpl as unknown as DefineComponent<
    TreePropsType,
    {},
    TreeGetters,
    {},
    TreeMethods,
    {},
    {},
    TreeEvents,
    string,
    PublicProps,
    ExtractPropTypes<TreePropsType>,
    ExtractPropTypes<TreePropsType>,
    SlotsType<TreeSlot>
>;

export default VxTree;
